<template>
  <div id="roles">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRole"></el-button>
        </el-col>
      </el-row>
      <el-table :data="roleList" border stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdbottom', index0 === 0 ? 'bdtop' : '']"
              v-for="(item0, index0) in scope.row.children"
              :key="item0.id"
              class="vcenter"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRights(scope.row, item0.id)">{{
                  item0.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  :class="['bdtop', index1 === 0 ? '' : 'bdtop']"
                  v-for="(item1, index1) in item0.children"
                  :key="item1.id"
                  class="vcenter"
                >
                  <!--  -->
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRights(scope.row, item1.id)"
                      >{{ item1.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item2 in item1.children"
                      :key="item2.id"
                      closable
                      @close="removeRights(scope.row, item2.id)"
                    >
                      {{ item2.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"
              ></el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="roleDelete(scope.row.id)"
              ></el-button
            >
            <el-button
              type="warning"
              icon="el-icon-setting"
              size="mini"
              @click="ShowRightDialog(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addRoleDialogVisible"
      width="50%"
      @close="addRoleDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addRoleDialogForm"
        :rules="addRoleDialogRules"
        ref="addRoleDialogFormRef"
        label-width="90px"
      >
        <!--  -->
        <el-form-item label="" prop="roleName">
          <el-input v-model="addRoleDialogForm.roleName"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addRoleDialogForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="sureAddRole"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showRightDialogVisible"
      width="50%"
      @close="ShowRightDialogClosed"
    >
      <!--  -->
      <el-tree
        :data="rightsList"
        :props="rightsListProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRightsList"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Roles",
  data() {
    return {
      roleList: [],
      addRoleDialogForm: {
        roleName: "",
        roleDesc: "",
      },
      addRoleDialogRules: {
        roleName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        roleDesc: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
      },
      addRoleDialogVisible: false,
      // 
      showRightDialogVisible: false,
      rightsList: [],
      rightsListProps: {
        label: "authName",
        children: "children",
      },
      defKeys: [],
      roleId: "",
    };
  },
  created() {
    this.getRolesList();
  },
  methods: {
    // 
    async getRolesList() {
      const { data: res } = await this.$http.get("roles");
      // console.log(res);
      if (res.meta.status != 200) {
        return this.$message.error("!");
      } else {
        this.roleList = res.data;
      }
    },
    addRole() {
      this.addRoleDialogVisible = true;
    },
    // 
    addRoleDialogClosed() {
      this.$refs.addRoleDialogFormRef.resetFields();
    },
    // 
    sureAddRole() {
      this.$refs.addRoleDialogFormRef.validate(async (valid) => {
        if (!valid) {
          return;
        }
        const { data: res } = await this.$http.post(
          "roles",
          this.addRoleDialogForm
        );
        if (res.meta.status != 201) {
          return this.$message.error("!");
        } else {
          this.getRolesList();
          this.$message.success("!");
        }
      });
    },
    // 
    roleDelete(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(`roles/${id}`);
          // console.log(res);
          if (res.meta.status != 200) {
            this.$message({
              type: "error",
              message: "!",
            });
          } else {
            this.$message({
              type: "success",
              message: "!",
            });
            this.getRolesList();
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    //
    removeRights(role, rightsId) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(
            `roles/${role.id}/rights/${rightsId}`
          );
          console.log(role.id);
          if (res.meta.status != 200) {
            this.$message({
              type: "error",
              message: "!",
            });
          } else {
            this.$message({
              type: "success",
              message: "!",
            });
            role.children = res.data;
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    //
    async ShowRightDialog(role) {
      const { data: res } = await this.$http.get(`rights/tree`);
      // console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("!");
      } else {
        this.rightsList = res.data;
        this.roleId = role.id;
      }
      // id
      this.getDefKeys(role, this.defKeys);
      this.showRightDialogVisible = true;
    },
    // iddefKeys 
    getDefKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => {
        this.getDefKeys(item, arr);
      });
    },
    // defKeys
    ShowRightDialogClosed() {
      this.defKeys = [];
    },
    // 
    async addRightsList() {
      const checkId = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ];
      const str = checkId.join(",");
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        {
          rids: str,
        }
      );
      if (res.meta.status !== 200) {
        return this.$message.error("!");
      } else {
        this.$message.success("!");
        this.getRolesList();
      }
    },
  },
};
</script>

<style scoped>
.bdbottom {
  border-bottom: 1px solid #eee;
}

.bdtop {
  border-top: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>